export default function () {
  var defaultWidth = 1,
    activeWidth = 2;

  function setToolWidth (width) {
    defaultWidth = width;
  }

  function getToolWidth () {
    return defaultWidth;
  }

  function setActiveToolWidth (width) {
    activeWidth = width;
  }

  function getActiveToolWidth () {
    return activeWidth;
  }

  var toolStyle = {
    setToolWidth,
    getToolWidth,
    setActiveWidth: setActiveToolWidth,
    getActiveWidth: getActiveToolWidth
  };

  return toolStyle;
}



